.display{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    background: $cyan-vd;
    border-radius: 12px;
    padding: 36px;

    &__wrap{
        display: flex;
        width: 100%;
        justify-content: space-between;

        color: $gr-cyan;
        font-weight: 700;

        margin-bottom: 48px;
    }

    &__labels{
        display: flex;
        flex-direction: column;
    }

    &__label{
        &-amt{
            color: $white;
            font-size: 14px;
        }

        &-person{
            font-size: 12px;
        }
    }

    &__value{
        font-size: 36px;
        line-height: 36px;
    }

    &__reset{
        background-color: $gr-cyan;
        font-family: 'Space Mono', monospace;
        font-weight: 700;
        font-size: 16px;
        outline: none;
        border: none;

        padding: 8px;

        color: $cyan-vd;

        border-radius: 4px;
        cursor: pointer;

        &:hover{
            background-color: $gr-cyan-l;
        }
    }
}

@media only screen and (max-width: 760px) {
    .display{
        padding: 36px 10%;

        &__wrap{
            flex-direction: column;
            margin-bottom: 24px;
        }

        &__labels{
            margin-bottom: 8px;
        }
    }
}

@media only screen and (max-width: 480px) {
    .display{
        margin-top: 32px;
        padding: 36px 8%;

        &__wrap{
            flex-direction: row;
            margin-bottom: 24px;
        }

        &__labels{
            margin-bottom: 8px;
        }
    }
}

@media only screen and (max-width: 360px) {
    .display{
        
        padding: 32px;

        &__wrap{
            flex-direction: column;
            margin-bottom: 24px;
        }

        &__labels{
            margin-bottom: 8px;
        }
    }
}